
header {
    margin-top: 3rem;
    text-align: center;
}

header h1 {
    font-family: 'Playfair Display SC', serif;
}

#cursive {
    font-family: 'Rouge Script', cursive;
    font-size: 4rem;
}

#new-img {
    margin-bottom: 1rem;
    background: #0abab5;
    color: #fff;
    border: none;
    position: relative;
    height: 50px;
    font-size: 1em;
    padding: 0 2em;
    transition: 800ms ease all;
/*    z-index: -10;*/
}

#new-img:hover {
    background: #fff;
    color: #0abab5;
}

#new-img:before,
#new-img:after {
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    height: 2px;
    width: 0;
    background: #0abab5;
    transition: 400ms ease all;
}

#new-img:after {
    right: inherit;
    top: inherit;
    left: 0;
    bottom: 0;
}

#new-img:hover:before,
#new-img:hover:after {
    width: 100%;
    transition: 800ms ease all;
}

